module.exports = class extends think.Controller {
	constructor(...arg) {
		super(...arg);
	}
	openAction() {
		this.emit('opend', 'This client opened successfully!');//给当前 socket 发送事件
		this.broadcast('joined', 'There is a new client joined successfully!');//给所有的 socket 广播事件
		return this.success();
	}
	closeAction() {
		console.log('ws close');
		return this.success();
	}
	getNoticeAction(){
		console.log('获取客户端 addUser 事件发送的数据', this.wsData);
		console.log('获取当前 WebSocket 对象', this.websocket);
		console.log('判断当前请求是否是 WebSocket 请求', this.isWebsocket);
		return this.success();
	}
};
